@import 'components/App/vars.scss';

#steps-counter {
  counter-reset: step;
  list-style-type: none;
  padding: 0;
  li {
    &::before {
      color: #ddd;
      display: inline-block;
      vertical-align: middle;
      margin-right: 0.1em;
      font-size: 4em;
      counter-increment: step;
      content: counter(step);
    }
  }
}
.img-mockup {
  height: 450px;
}

.profile-col {
  padding-bottom: 2rem;
  .profile-bloc {
    background-color: darkslategray;
    border-radius: 5px;
    color: #fff;
    padding: 5rem 3rem;
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 2rem;
    background-size: cover;
    &.profile-bloc-donor {
      background-image: url('../../assets/img/donor.jpg');
    }
    &.profile-bloc-beneficiary {
      background-image: url('../../assets/img/beneficiary.jpg');
    }
    &.profile-bloc-shopkeeper {
      background-image: url('../../assets/img/shopkeeper.jpg');
    }
  }
}

#join-us {
  a {
    text-decoration: underline;
  }
}

.testimony {
  position: relative;
  padding-bottom: 3rem;
  .testimony-metas {
    margin-top: 1rem;
    img {
      border-radius: 50%;
      margin-right: 0.5rem;
      max-width: 40px;
    }
  }
  &:before {
    content: ' ';
    width: 144px;
    height: 112px;
    background-image: url('../../assets/img/quote.png');
    position: absolute;
    top: -2rem;
    left: -1rem;
    opacity: 0.3;
  }
}

.page-id-home {
  h2 {
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-bottom: 3rem;
    margin-top: 1.7rem;
  }
}
